

































































































































import { Component, Vue } from 'vue-property-decorator'
import { Action } from 'vuex-class'

declare var $: any

@Component({})
export default class login extends Vue {
  innerheight: any = 0
  forgetPass: boolean = false
  @Action('Login') Login: any

  private loading = false

  mounted(): void {
    this.resizeContentHeight()
  }

  public resizeContentHeight() {
    this.innerheight = window.innerHeight
  }

  login() {
    const loginParams = {}
    this.loading = true
    this.Login(loginParams).then((res: any) => this.loginSuccess(res))
  }

  requestFailed(err: any) {}

  loginSuccess(res: any) {
    this.loading = false
    // // 校验并删除过期字典数据
    location.href = '/index.html/#/'
  }
}
